import Duck from 'reduck';
import SEARCH from '../redux/actions';

const initialState = {
  loading: false,
  search: '',
  error: '',
  items: [],
};

const duck = new Duck('characters', initialState);

export const search = duck.defineAction(SEARCH, {
  creator(searchString) {
    return {
      payload: { searchString },
      meta: {
        promise: {
          method: 'GET',
          url: '/characters/' + searchString,
        },
      },
    };
  },
  reducer(state, { payload }) {
    return {
      ...state,
      loading: true,
      search: payload.searchString,
    };
  },
  resolve(state, { payload }) {
    return {
      ...state,
      error: '',
      loading: false,
      items: [
        ...state.items,
        payload.data.map(character => {
          const { name, male, imageLink, culture, house } = character;
          return { name, male, imageLink, culture, house };
        }),
      ],
    };
  },
  reject(state) {
    return {
      loading: false,
      error: 'Oops, we got a network error '
    }
  }
});

export default duck.reducer;



